//COLORS
$togglerMenu: #faff00;

// HOME PAGE
@media screen and (max-width: 1200px) {
  nav {
    padding: 12px !important;
    .navabr-brand {
      font-size: 20px;
      padding: 0 10px;
    }
    .navbar-toggler {
      background-color: $togglerMenu;
      border-radius: 0;
      span {
        font-size: 15px;
      }
    }
    .navbar-nav {
      .nav-iteam {
        margin: 20px 20px !important;
        font-size: 18px !important;
        text-align: center;
      }
    }
  }
  .aboutinfo {
    display: flex;
    padding: 75px 35px;
    .side2 {
      .myImage {
        width: 330px;
      }
    }
    .side1 {
      width: 100%;
      h1 {
        font-size: 50px !important;
      }
      p {
        font-size: 20px !important;
        width: 75% !important;
        margin: 15px 0 25px !important;
      }
      .buttons {
        .linkBtn {
          padding: 10px 10px;
          font-size: 20px;
        }
      }
      .socailNet {
        .links {
          padding: 0 10px 0 10px;
          svg {
            font-size: 20px;
          }
        }
      }
    }
  }
}

