//PORTFOLIO
@media screen and (max-width: 1200px) {
  .titles {
    h3 {
      font-size: 35px !important;
    }
    p {
      font-size: 25px !important;
    }
  }
  .row {
    .col-md-12 {
      .projectCard {
        display: flex;
        .projectImage {
          width: 50%;
        }
        .projextTitles {
          margin: 30px 50px;
          .projectAbout {
            .projectName {
              font-size: 25px;
            }
            .projectDesc {
              font-size: 18px;
            }
          }
        }
        .projectButtons {
          padding: 2px;
          a {
            padding: 15px 10px !important;
            font-size: 16px !important;
            &:nth-child(2) {
              margin-left: 13px !important;
            }
          }
        }
      }
      &:nth-child(2) {
        .projectCard {
          .projectTitles {
            .projectAbout {
              width: 75%;
            }
          }
        }
      }
    }
  }
}

