.profile-container {
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    height: 100%;
    width: 100%;
    min-height: -webkit-fit-content;
    min-height: -moz-fit-content;
    min-height: fit-content;
    background-color: rgb(8, 4, 41);
    /* background-image: url("../../styles/images/bek1.jpg"); */
    /* background-size: cover; */
}
.profile-btn{
    padding: 1%;
    border: chartreuse;
}

.profile-parent{
    display: flex;
    align-items: center;
    color: aliceblue;
}
.profile-details-name {
    font-size: 24px;
    font-family: "Poppins SemiBold";
}
.profile-details-role {
    display: flex;
    flex-direction: column;
    margin: 14px 0 24px 0;
}
.profile-details-role h1{
    font-size: 40px;
    font-family: cursive;
}
.primary-text {
    color: #fff;
}
.highlighted-text{
    color: (--dark-orange);
    color: #FF5823;
}
.profile-role-tagline{
    font-size: 19px;
    margin:5px 0 0 0;
    font-family: "Poppins Light";
}
.profile-options .highlighted-btn {
    padding: 8px;
    margin: 0 0 0 28px;
}
.profile-options .highlighted-btn:hover {
    margin: 0 0 0 28px;
    background-color: cornsilk;
    color: #111;
}
.profile-picture{
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 1px 0px 0px #fff;
    border-radius: 50%;
    height: 380px;
    width: 380px;
    margin: 0 0px 0 100px;
}
.profile-picture-backgroud {
    height: 92%;
    width: 92%;
    background-image: url(/static/media/bek1.67cbe2d2.jpg);
    background-size: cover;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-position: center;
}
.profile-picture-backgroud:hover {
    -webkit-transform: scale(1.07);
            transform: scale(1.07);
    transition: 1s ease-out;
}
.colz .colz-icon i {
    color: white;
    padding: 8px;
    padding-left: 0px;
}
.colz .colz-icon:hover i {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}

@media screen and (max-width: 1110px) {
    .profile-picture{
        margin: 0 0px 0 80px;
    }
    .profile-details-name{
        font-size: 20px;
    }
    .profile-details-role .primary-text{
       font-size: 12px;
    }
    .profile-options{
        width: 120px;
        padding: 11px 0;
        z-index: 700;
        display: flex;
    }

}
@media screen and (max-width: 910px) {
    .profile-picture{
        height: 320px;
        width: 320px;
        margin: 0 0 0 4px;
    }
}
@media screen and (max-width: 767px) {
    .profile-picture{
        margin: 0 150px!important;
        align-items: center;
    }
    .profile-parent {
        flex-direction: column-reverse;
        margin: 40px 0 0 0;
    }
    .profile-options {
       display: flex;
       justify-content: space-between;
    }
    .profile-details {
        margin: 25px 0 0;
    }
    .profile-options .highlighted-btn {
        margin: 0 !important;
    }
    .profile-options 
    .profile-details-role {
        align-items: center;
    }
}
@media screen and (max-width: 500px) {
    .profile-picture{
        margin: 0 100px !important;
    }
    .profile-parent {
        flex-direction: column-reverse;
        margin: 40px 0 0 0;
    }
    .profile-options {
       display: flex;
       justify-content: space-between;
    }
    .profile-details {
        margin: 25px 0 0;
    }
    .profile-options .highlighted-btn {
        margin: 0 !important;
    }
    .profile-options 
    .profile-details-role {
        align-items: center;
    }
}
@media (min-width: 320px) and (max-width: 330px) {
    .profile-picture {
        width: 100%;
        height: 200px;
        margin: 0 30px!important;
    }
    .profile-details {
        width: 90%;
    }
}

