/* Loading Page */

.mainPage{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    overflow: hidden;
}

.center{
    position: relative;
    width: 400px;
}
.center div{
    position: relative;
    height: 100px;
    margin: -30px 0;
    z-index: 2;
    -webkit-transform:  skewY(-5deg);
            transform:  skewY(-5deg);
    display: flex;
    justify-content: center;
    align-items: center;
}
.center div:nth-child(2),
.center div:nth-child(4)
{
    -webkit-transform: skewY(14.5deg);
            transform: skewY(14.5deg);
    z-index: 1;
}
.center div:nth-child(4)
{
    -webkit-transform: skewY(14.5deg);
            transform: skewY(14.5deg);
    -webkit-transform-origin: left;
            transform-origin: left;
    top: -52px;
}
.center div::before
{
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
}
.center div:nth-child(1):before,
.center div:nth-child(3):before
{
    background: linear-gradient(-160deg, #ff0058, #673ab7);
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
}
.center div:nth-child(2):before,
.center div:nth-child(4):before{
    background: linear-gradient(-20deg, #ff0058, #38009c);
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
}
.center div:nth-child(1):before {
-webkit-animation: animate 1s linear forwards;
        animation: animate 1s linear forwards;
-webkit-transform-origin: right;
        transform-origin: right;
-webkit-animation-delay: 9s;
        animation-delay: 9s;
}
.center div:nth-child(2):before {
-webkit-animation: animate 1s linear forwards;
        animation: animate 1s linear forwards;
-webkit-transform-origin: left;
        transform-origin: left;
-webkit-animation-delay: 10s;
        animation-delay: 10s;
}
.center div:nth-child(3):before {
-webkit-animation: animate 1s linear forwards;
        animation: animate 1s linear forwards;
-webkit-transform-origin: right;
        transform-origin: right;
-webkit-animation-delay: 11s;
        animation-delay: 11s;
}
.center div:nth-child(4):before {
-webkit-animation: animate 1s linear forwards;
        animation: animate 1s linear forwards;
-webkit-transform-origin: left;
        transform-origin: left;
-webkit-animation-delay: 12s;
        animation-delay: 12s;
width: 60%;
left: 0;
}
@-webkit-keyframes  animate {
0%
{
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
}
100%
{
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
}
}
@keyframes  animate {
0%
{
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
}
100%
{
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
}
}
.center div:nth-child(1):after,
.center div:nth-child(3):after{
content: '';
position: absolute;
bottom: 0;
left: 0;
width: 100%;
height: 50%;
background: rgba(255, 255, 255, .2);
}
.center div h2{
position: relative;
margin: 0;
padding: 0;
z-index: 10;
opacity: 0;
color: #fff;
}
.center div:nth-child(1) h2 {
-webkit-animation: fadeText 0.5s linear forwards;
        animation: fadeText 0.5s linear forwards;
-webkit-animation-delay: 12s;
        animation-delay: 12s;
font-size: 40px;
}
.center div:nth-child(3) h2 {
-webkit-animation: fadeText 0.5s linear forwards;
        animation: fadeText 0.5s linear forwards;
-webkit-animation-delay: 12s;
        animation-delay: 12s;
font-size: 90px;
font-weight: 800;
}
@-webkit-keyframes fadeText {
0%{
    opacity: 0;
}
100%{
    opacity: 1;
}
}
@keyframes fadeText {
0%{
    opacity: 0;
}
100%{
    opacity: 1;
}
}

.loading{
position: fixed;
top: 0;
left: 0;
width: 100%;
height: 100%;
background: #fff;
display: flex;
justify-content: center;
align-items: center;
z-index: 15;
-webkit-animation: fadeout 7s linear forwards;
        animation: fadeout 7s linear forwards;
}
@-webkit-keyframes fadeout{
0%,91%{
    opacity: 1;
    visibility: visible;
}
100%{
    opacity: 0;
    visibility: hidden;
}
}
@keyframes fadeout{
0%,91%{
    opacity: 1;
    visibility: visible;
}
100%{
    opacity: 0;
    visibility: hidden;
}
}
.loading h2{
color: #000;
}
.loading .bar{
position: relative;
width: 400px;
height: 40px;
background: transparent;
margin: 0 20px;
border: 2px solid #000;
box-sizing: border-box;
}
.loading .bar::before{
content: '';
position: absolute;
top: 0;
left: 0;
width: 100%;
height: 100%;
background: #000;
-webkit-transform-origin: left;
        transform-origin: left;
-webkit-animation: animate 5s linear forwards;
        animation: animate 5s linear forwards;
}
.loading .bar:after{
content: 'Loading...';
position: absolute;
top: 0;
left: 0;
width: 100%;
height: 100%;
font-weight: 800;
text-transform: uppercase;
letter-spacing: 10px;
text-align: center;
line-height: 36px;
color: #fff;
