.newyear{
    background: url("./images/Backgroundfont.jpg") no-repeat;
    margin: 0;
    padding: 0;
    height: 100vh;
}

.container{
    margin: 15% auto;
    text-align: center;
}
.container > h2{
    font-size: 2.7rem;
    color: #ffffff;
    font-family: system-ui;
    margin-bottom: 0;
}
.container > h3 {
    font-size: 2.5rem;
    margin: 0;
    color: #9c9c9c;
    font-family: sans-serif;
}
.container > .general{
    display: flex;
    justify-content: center;
}
.general > .box{
    margin: 20px;
    font-size: 1.5rem;
    color: #ffffff;
    font-family: system-ui;
}
.box > h4{
    background-color: #e9ff00;
    padding: 0.7rem;
    color: #000000;
}

/* Responsive */
@media screen and (max-width: 768px) {
    .container {
        margin: 15% auto;
    }
    .container > h2{
        font-size: 2.3rem;
    }
    .container > h3 {
        font-size: 2.2rem;
    }
    .container > .general{
        display: block;
        justify-content: center;
    }
    .general > .box{
        font-size: 1rem;
    }
  }

@media screen and (max-width: 500px) {
    .container {
        margin: 15% auto;
    }
    .container > h2{
        font-size: 2.3rem;
    }
    .container > h3 {
        font-size: 2.2rem;
    }
    .container > .general{
        display: block;
        justify-content: center;
    }
    .general > .box{
        margin: 30px;
        font-size: 1rem;
    }
  }

@media screen and (max-width: 370px) {
    .container {
        margin: 15% auto;
    }
    .container > h2{
        font-size: 2.3rem;
    }
    .container > h3 {
        font-size: 2.2rem;
    }
    .container > .general{
        display: block;
        justify-content: center;
    }
    .general > .box{
        margin: 35px;
        font-size: 1rem;
    }
  }

  
@media (min-width: 320px) and (max-width: 330px) {
    .container {
        margin: 10% auto;
    }
    .container > h2{
        font-size: 2.3rem;
    }
    .container > h3 {
        font-size: 2.2rem;
    }
    .container > .general{
        display: block;
        justify-content: center;
    }
    .general > .box{
        margin: 40px;
        font-size: 1rem;
    }
  }



