//FONTS
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,400;0,700;1,200&display=swap");

///COLOR
$homeButtonColor: #2b2b2b;
$homeButtonColor2: #faff00;
$socialIcons: #faff00;

//MIXINS

@mixin PageTitle1 {
  font-size: 45px;
  color: #fff;
  margin: 0;
  font-weight: bold;
  font-family: "Montserrat", sans-serif;
  letter-spacing: 2px;
}
@mixin PageTitle2 {
  font-size: 30px;
  color: $homeButtonColor2;
  font-family: "Montserrat", sans-serif;
  letter-spacing: 0.7px;
}
@mixin laptopSpecs {
  ul {
    a {
      display: flex;
      text-decoration: none;
      cursor: pointer;
      svg {
        color: #ffffff !important;
        font-size: 30px;
        margin: 0 5px;
        transition: 0.3s ease;
        -webkit-transition: 0.3s ease;
        -moz-transition: 0.3s ease;
      }
      h2 {
        font-family: "Montserrat" sans-serif;
        color: #ffffff;
      }
      &:hover svg {
        transform: rotate(35deg);
      }
    }
    li {
      color: $socialIcons;
      font-family: "Montserrat", sans-serif;
      margin: 20px 30px 10px;
      font-size: 20px;
      width: 100%;
    }
  }
}

@mixin keyboardSpecs {
  ul {
    h2 {
      font-family: "Montserrat", sans-serif;
      color: #ffffff;
      font-weight: 600;
    }
    li {
      color: $socialIcons;
      font-family: "Montserrat", sans-serif;
      margin: 20px 30px 10px;
      font-size: 20px;
      width: 100%;
    }
  }
}

html {
  overflow-x: hidden;
}
body {
  background-color: #000000 !important;
  overflow-x: hidden;
}

// scrollbar
::-webkit-scrollbar {
  width: 3px !important;
}
::-webkit-scrollbar-thumb {
  background-color: $socialIcons;
  border-radius: 50px !important;
}
::-webkit-scrollbar-track {
  background-color: #000000;
}

//nav

nav {
  padding: 15px 80px !important;
  animation: animNav 1s ease;
  background-color: #000000;
  .navbar-brand {
    font-size: 25px;
    color: #ffffff !important;
    font-family: "Montserrat", sans-serif;
    b {
      //logo bold text
      font-weight: bold;
    }
  }
//winter
  .navbar-nav {
    margin-left: auto;
    font-family: "Frozen";
    src: url('./style/font.woff');
    .nav-iteam {
      margin: 2px;
      font-size: 20px;
      font-weight: 500;
    }
    a {
      // color: #949494 !important;
      color: rgb(10, 255, 243);
      text-decoration: none;
      transition: 0.2s ease;
      -webkit-transition: 0.2s ease;
      margin: 10px;
      &:hover {
        color: #ffe608 !important;
        text-decoration: none;
      }
    }


    //Resume
    .resumeColor {
      color: $socialIcons;
    }
    //
  }
}
//nav fexid
nav.fixed {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  backdrop-filter: blur(10px);
  background-color: #000000cf;
  box-shadow: 0 5px 15px #0c0c0cde;
}

// about
.aboutinfo {
  display: flex;
  justify-content: center;
  flex-direction: row-reverse;
  padding: 70px;
  .side1 {
    width: 55%;
    margin: 20px 0;
    .title {
      font-size: 70px;
      font-family: "Montserrat", sans-serif;
      color: #ffffff;
    }
    .description {
      width: 75%;
      color: #aaaaaa;
      margin: 15px 3px 60px;
      font-size: 20px;
      font-family: "Montserrat", sans-serif;
      font-weight: 700;
    }
    .typetext {
      margin: 30px;
      span {
        color: rgb(20, 209, 209);
      }
    }
    .buttons {
      .linkBtn {
        background-color: $homeButtonColor;
        padding: 17px 40px;
        color: #fff;
        font-size: 20px;
        text-decoration: none;
        font-family: "Montserrat", sans-serif;
        font-weight: 500;
        &:nth-child(2) {
          margin-left: 10px;
          background-color: $homeButtonColor2;
          color: #000000;
        }
      }
    }
    .socialNet {
      margin-top: 45px;
      .links {
        padding: 0 30px 0 0;
        .iconSvg {
          font-size: 35px;
          color: #fff;
          &:hover {
            color: $socialIcons;
          }
        }
      }
    }
  }
  .side2 {
    .myImage {
      width: 40%;
    }
  }
}
//portfolio
.titles {
  text-align: center;
  margin-top: 75px;
  .title1 {
    @include PageTitle1();
  }
  .title2 {
    @include PageTitle2();
  }
}
.row {
  .col-md-12 {
    padding-top: 50px;
    .projectCard {
      display: flex;
      justify-content: space-around;
      margin: 30px 0;
      .projectImage {
        width: 40%;
      }
      .projectTitles {
        margin: 50px 10px;
        .projectAbout {
          .projectName {
            color: #fff;
            font-weight: 600;
            font-family: "Montserrat", sans-serif;
          }
          .projectDesc {
            color: #aaaaaa;
            margin: 10px 0 30px;
            font-family: "Montserrat", sans-serif;
          }
        }
        .projectButtons {
          .buttons {
            background-color: #2b2b2b;
            padding: 17px 35px;
            color: #fff;
            font-size: 20px;
            text-decoration: none;
            font-family: "Motserrant", sans-serif;
            font-weight: 500;
            svg {
              margin: 0 10px 0;
            }
            &:nth-child(2) {
              background-color: $homeButtonColor2;
              color: #000000;
              margin-left: 30px;
            }
          }
        }
      }
    }
    &:nth-child(2) {
      .projectCard {
        flex-direction: row-reverse;
        .projectTitles {
          margin: 30px 0;
          .projectAbout {
            .projectDesc {
              width: 75%;
            }
          }
        }
      }
    }
  }
}
//setup
.setupMain {
  .setupImage {
    display: flex;
    justify-content: center;
    img {
      margin-top: 100px;
      width: 50%;
      border-radius: 10px;
    }
  }
  .container {
    .row {
      justify-content: center;
      .col-md-8 {
        .mySetup {
          padding: 50px 75px;
          display: grid;
          grid-template-columns: auto auto;
          .laptopSpecs {
            margin-bottom: 40px;
            @include laptopSpecs();
          }
          .keyboardSpecs {
            width: 100%;
            @include keyboardSpecs();
          }
        }
      }
    }
  }
}

//about-me
.aboutMain {
  .container {
    display: flex;
    padding: 30px;
    .myImage {
      width: 100%;
      height: 600px;
    }
    //profile new content
    .profile-picture {
      display: flex;
      align-items: center;
      justify-content: center;
      box-shadow: 0 1px 0px 0px #fff;
      border-radius: 50%;
      height: 380px;
      width: 380px;
      margin: 0 0px 0 100px;
    }
    .profile-picture-backgroud {
      height: 92%;
      width: 92%;
      background-image: url("../styles/images/bek1.jpg");
      background-size: cover;
      border-radius: 50%;
      background-repeat: no-repeat;
      background-position: center;
    }
    .profile-picture-backgroud:hover {
      transform: scale(1.07);
      transition: 1s ease-out;
    }

    //
    span {
      .aboutmeText {
        padding: 0 0 30px 45px;
        font-size: 26px;
        width: 95%;
        color: #ffffff;
        font-family: "Monrserrat", sans-serif;
        div {
          padding: 30px 0 30px;
        }
      }
      .resumebtn{
        padding: 8px;
        text-decoration: none;
        color: cyan;
        border: 2px solid cyan;
        border-radius: 8px;
      }
      .resumebtn:hover{
        color: $homeButtonColor2;
        border: 2px solid $homeButtonColor2;
      }
    }
  }
}

//footer
.footer {
  padding: 20px 0 0;
  border-top: 1px solid #aaaaaa;
  text-align: center;
  p {
    font-size: 17px;
    color: #fff;
    font-family: "Montserrat", sans-serif;
    svg {
      color: rgb(255, 0, 0);
    }
    .redEye{
      color: blue;
    }
  }
}

// CONTACT ME

.contactMain {
  width: 100%;
  justify-content: center;
  .card {
    width: 50%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    background-color: rgb(6, 33, 83);
    .images {
      width: 90%;
      background-image: url("../styles/images/n.jpg");
      background-repeat: no-repeat;
      background-size: cover;
    }
    .col {
      margin: 0 30px 0 30px;
      color: cyan;
      .adressText {
        display: block;
        width: 100%;
        color: rgb(9, 77, 155);
        .bigText{
          color: rgb(12, 186, 209);
        }
      }
      .adressText:hover {
        color: $socialIcons;
      }
      .socialNet {
        display: flex;
        justify-content: space-around;
        .links {
          margin: 10px;
          .iconSvg:hover {
            color: $socialIcons;
          }
        }
      }
    }
  }
  .card2 {
    justify-content: center;
    .row {
      margin: 20px;
      .form {
        width: 500px;
        background-color: #222;
        display: flex;
        justify-content: center;
        flex-direction: column;
        border-radius: 10px;
        h1 {
          margin-bottom: 30px;
          color: rgb(12, 186, 209);
        }
        textarea {
          padding: 20px;
          border-radius: 3px;
          margin-bottom: 20px;
          border: 2px solid rgb(241, 3, 3);
          background: #222;
          font-size: 16px;
          color: rgb(0, 0, 32);
          outline: none;
        }
        textarea:hover {
          border: 2px solid rgb(238, 204, 10);
        }

        .form > textarea {
          height: 150px;
          max-width: 400px;
          min-height: 100px;
        }
        .inps {
          width: 100%;
          color: cyan;
          background: #00a0eb;
          border-radius: 3px;
        }
        .inps:focus {
          color: cyan;
          background-color: darkblue;
        }

        .labels {
          padding-bottom: 10px;
          color: cyan;
          font-weight: bold;
        }
        .col {
          justify-content: center;
          flex-direction: column;
          width: 100%;
          height: 200px;
          .buttons {
            width: 100px;
            background-color: rgb(45, 45, 153);
            border-radius: 5px;
            color: #fff;
            cursor: pointer;
            margin: 10px 180px;
          }
          .buttons:hover {
            background-color: rgb(0, 0, 196);
            color: rgb(2, 248, 2);
          }
        }
      }
    }
  }
}


//ERROR PAGE

.errPage{
  display: grid;
  width: 100%;
  height: 100vh;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  .row{
    margin: 20px 20px 20px;
    display: grid;
    width: 100%;
    justify-content: center;
    align-items: center;;
    background: linear-gradient(180deg, blue,rgb(51, 255, 0));
    border-radius: 20px;
    .error{
      font-size: 150px;
    }
    .col{
      margin: 20px;
      .errtext{
        color: darkblue;
      }
    }
    .homePage{
      text-decoration: none;
      color: rgb(0, 47, 255);
  }
  }
}
